'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';

const Header = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [cartCount, setCartCount] = useState(0);
  const [isSearchOpen, setIsSearchOpen] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [isNotificationsOpen, setIsNotificationsOpen] = useState(false);
  const [isUserMenuOpen, setIsUserMenuOpen] = useState(false);
  const [notifications, setNotifications] = useState([
    {
      id: 1,
      title: "طلب جديد",
      message: "تم تأكيد طلبك #ORD-001 بنجاح",
      time: "منذ 5 دقائق",
      read: false
    },
    {
      id: 2,
      title: "تحديث الطلب",
      message: "طلبك #ORD-002 قيد التوصيل",
      time: "منذ ساعة",
      read: false
    },
    {
      id: 3,
      title: "عرض خاص",
      message: "خصم 20% على جميع باقات الورود الحمراء",
      time: "منذ يوم",
      read: true
    }
  ]);

  // تحديث عداد السلة
  useEffect(() => {
    const updateCartCount = () => {
      const cart = JSON.parse(localStorage.getItem('cart') || '[]');
      const totalItems = cart.reduce((sum: number, item: any) => sum + (item.quantity || 1), 0);
      setCartCount(totalItems);
    };

    updateCartCount();
    
    // الاستماع لتغييرات السلة
    window.addEventListener('storage', updateCartCount);
    window.addEventListener('cartUpdated', updateCartCount);

    return () => {
      window.removeEventListener('storage', updateCartCount);
      window.removeEventListener('cartUpdated', updateCartCount);
    };
  }, []);

  // إغلاق الإشعارات عند النقر خارجها
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      const target = event.target as HTMLElement;
      if (!target.closest('.notifications-dropdown') && !target.closest('.notifications-button')) {
        setIsNotificationsOpen(false);
      }
      if (!target.closest('.user-menu-dropdown') && !target.closest('.user-menu-button')) {
        setIsUserMenuOpen(false);
      }
    };

    if (isNotificationsOpen || isUserMenuOpen) {
      document.addEventListener('mousedown', handleClickOutside);
    }

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [isNotificationsOpen, isUserMenuOpen]);

  // حساب عدد الإشعارات غير المقروءة
  const unreadCount = notifications.filter(n => !n.read).length;

  // تعيين الإشعار كمقروء
  const markAsRead = (id: number) => {
    setNotifications(prev => 
      prev.map(notification => 
        notification.id === id 
          ? { ...notification, read: true }
          : notification
      )
    );
  };

  // تعيين جميع الإشعارات كمقروءة
  const markAllAsRead = () => {
    setNotifications(prev => 
      prev.map(notification => ({ ...notification, read: true }))
    );
  };

  // تسجيل الخروج
  const handleLogout = () => {
    // هنا يمكن إضافة منطق تسجيل الخروج
    alert('تم تسجيل الخروج بنجاح');
    setIsUserMenuOpen(false);
  };

  return (
    <header className="bg-white/95 backdrop-blur sticky top-0 z-50 border-b border-gray-100">
      <div className="max-w-7xl mx-auto px-3 sm:px-6 lg:px-8">
        {/* Row: Icons left + centered logo + actions right */}
        <div className="grid grid-cols-3 items-center h-20">
          {/* Left: Account + Cart */}
          <div className="flex items-center gap-6 text-[#134850]">
            <Link href="/profile" className="flex items-center gap-1 hover:opacity-80 transition">
              <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
              </svg>
              <span className="hidden sm:inline text-sm" style={{fontFamily:'var(--font-almarai)'}}>حسابي</span>
            </Link>
            <Link href="/cart" className="relative flex items-center gap-1 hover:opacity-80 transition">
              <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 3h2l.4 2M7 13h10l4-8H5.4m0 0L7 13m0 0l-2.5 5M7 13l2.5 5m6-5v6a2 2 0 01-2 2H9a2 2 0 01-2-2v-6m8 0V9a2 2 0 00-2-2H9a2 2 0 00-2 2v4.01" />
              </svg>
              <span className="hidden sm:inline text-sm" style={{fontFamily:'var(--font-almarai)'}}>سلة</span>
              {cartCount > 0 && (
                <span className="absolute -top-1 -right-2 bg-red-500 text-white text-[10px] rounded-full h-4 w-4 flex items-center justify-center">{cartCount}</span>
              )}
            </Link>
          </div>

          {/* Center: Logo */}
          <div className="flex items-center justify-center">
            <Link href="/" className="flex items-center">
              <img src="/images/log.png" alt="زهور الشمس" className="h-9 w-auto object-contain" />
            </Link>
          </div>

          {/* Right: Search + Menu */}
          <div className="ml-auto flex items-center justify-end gap-6 text-[#134850]">
            <button onClick={() => setIsMenuOpen(true)} className="flex items-center gap-1 hover:opacity-80 transition">
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
              </svg>
              <span className="hidden sm:inline text-sm" style={{fontFamily:'var(--font-almarai)'}}>القائمة</span>
            </button>
            <button onClick={() => setIsSearchOpen(!isSearchOpen)} className="flex items-center gap-1 hover:opacity-80 transition">
              <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
              </svg>
              <span className="hidden sm:inline text-sm" style={{fontFamily:'var(--font-almarai)'}}>البحث</span>
            </button>
          </div>
        </div>

        {/* Search Bar */}
        {isSearchOpen && (
          <div className="border-t border-gray-100 py-4">
            <div className="max-w-2xl mx-auto">
              <div className="relative">
                <input
                  type="text"
                  placeholder="ابحث عن الورود والباقات..."
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  className="w-full px-4 py-2 pr-10 border border-gray-300 rounded-full focus:outline-none focus:ring-2 focus:ring-[#134850]"
                  style={{fontFamily:'var(--font-almarai)'}}
                />
                <button className="absolute right-3 top-1/2 -translate-y-1/2 text-[#134850]">
                  <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                  </svg>
                </button>
              </div>
            </div>
          </div>
        )}

        {/* Right-side Drawer Navigation */}
        {isMenuOpen && (
          <div className="fixed inset-0 z-50">
            {/* overlay */}
            <button aria-label="overlay" onClick={() => setIsMenuOpen(false)} className="absolute inset-0 bg-black/40"></button>
            {/* drawer */}
            <div className="absolute right-0 top-0 h-full w-96 max-w-[95vw] bg-white shadow-xl border-s border-gray-100 transform transition-transform duration-300 translate-x-0">
              <div className="p-4 flex items-center justify-between border-b">
                <span className="text-base font-medium text-[#134850]" style={{fontFamily:'var(--font-almarai)'}}>القائمة</span>
                <button onClick={() => setIsMenuOpen(false)} className="p-2 hover:opacity-70" aria-label="اغلاق">
                  <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                  </svg>
                </button>
              </div>
              <nav className="p-2">
                <Link href="/" onClick={() => setIsMenuOpen(false)} className="block px-3 py-3 rounded-lg text-gray-700 hover:bg-gray-50">الرئيسية</Link>
                <Link href="/bouquets" onClick={() => setIsMenuOpen(false)} className="block px-3 py-3 rounded-lg text-gray-700 hover:bg-gray-50">الباقات الجاهزة</Link>
                <Link href="/custom" onClick={() => setIsMenuOpen(false)} className="block px-3 py-3 rounded-lg text-gray-700 hover:bg-gray-50">تنسيق خاص</Link>
                <Link href="/occasions" onClick={() => setIsMenuOpen(false)} className="block px-3 py-3 rounded-lg text-gray-700 hover:bg-gray-50">المناسبات</Link>
                <Link href="/blog" onClick={() => setIsMenuOpen(false)} className="block px-3 py-3 rounded-lg text-gray-700 hover:bg-gray-50">المدونة</Link>
                <Link href="/contact" onClick={() => setIsMenuOpen(false)} className="block px-3 py-3 rounded-lg text-gray-700 hover:bg-gray-50">تواصل معنا</Link>
              </nav>
            </div>
          </div>
        )}
      </div>
    </header>
  );
};

export default Header;
